@tailwind base;
@tailwind components;
@tailwind utilities;

/* Reset mínimo e suavização de fontes */
* { box-sizing: border-box; }
html, body { height: 100%; }
body { -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

/* Normalização de links para evitar azul padrão do navegador */
@layer base {
  a { color: inherit; text-decoration: none; }
}

/* Painel direito com estrelas sutis e gradiente noturno */
.login-night-bg {
  background-image:
    radial-gradient(1200px 600px at 80% 30%, rgba(122,92,255,0.25), transparent 60%),
    radial-gradient(800px 500px at 70% 70%, rgba(255,139,167,0.18), transparent 65%),
    linear-gradient(135deg, #0a0a12 0%, #12091f 45%, #2b0f2f 100%);
}

.login-stars { position: absolute; inset: 0; pointer-events: none; }
.login-stars::before,
.login-stars::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image:
    radial-gradient(2px 2px at 20% 30%, rgba(255,255,255,0.6), transparent 40%),
    radial-gradient(1.5px 1.5px at 40% 60%, rgba(255,255,255,0.5), transparent 40%),
    radial-gradient(1.5px 1.5px at 65% 20%, rgba(255,255,255,0.6), transparent 40%),
    radial-gradient(1px 1px at 80% 55%, rgba(255,255,255,0.6), transparent 40%),
    radial-gradient(1px 1px at 90% 80%, rgba(255,255,255,0.5), transparent 40%);
  animation: twinkle 8s ease-in-out infinite;
  opacity: 0.6;
}
.login-stars::after { animation-duration: 10s; opacity: 0.4; }

/* Utilitário para um leve brilho no botão */
.shadow-glow { box-shadow: 0 10px 30px rgba(122, 92, 255, 0.25); }
